	Author: Cora
	Date:2020/11/1
	Version: updated
	备注：卡片弹出效果待修改
-->

<template>
	<view class="camp-card">
		<view class="post" @click="showReply = !showReply">
			<view class="head">
				<image class="pentagon" src="../../static/image/pentagon.png"></image>
				<view class="head-text">{{option.userId}}</view>
			</view>
			<view class="content">
				<view class="content-text">{{option.articleContent}}</view>
			</view>
			<text class="head-time">{{option.createDate}}</text>
		</view>
		<view class="foot">
			<view class="foot-reply" v-if="showReply">
				<view class="foot-content">
					{{option.articleContent}}
				</view>
				<view class="foot-head">
					{{option.userId}}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'CampCard',
		props: {
			option: {
				articleContent: {
					type: String,
					default: ''
				},
				userId: {
					type: String,
					default: ''
				},
				createDate: {
					type: String,
					default: ''
				},
			},
		},
		data() {
			return {
				reply: "",
				showReply: false,
			}
		},
		mounted() {
			console.log(this.option);
		}
	}
</script>

<style>
	.camp-card {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 50upx;
		margin-bottom: 50upx;
	}
	
	.post {
		display: flex;
		flex-direction: column;
		width: 650upx;
		background: #444444;
		border-radius: 15upx 15upx 0upx 0upx;
	}
	
	.head {
		display: flex;
		padding-left: 16px;
		padding-top: 16px;
	}
	
	.head-text {
		display: flex;
		height: 27upx;
		font-size: 21upx;
		font-weight: bold;
		color: #888888;
	}
	
	.content {
		display: flex;
		overflow: auto;
		padding: 50upx;
	}
	
	.content-text {
		display: flex;
		width: 550upx;
		font-size: 29upx;
		color: #FFFFFF;
		opacity: 0.8;
		line-height: 45upx;
		letter-spacing: 2upx;
	}
	
	.head-time {
		font-size: 10px;
		color: #D5D5D5;
		max-width: 80px;
		display: inline-block;
		line-height: 10px;
		margin-left: 14px;
		margin-bottom: 12px;
	}
	
	.foot {
		display: flex;
		flex-direction: column;
		padding: 15upx;
		width: 560upx;
		background: #585858;
		box-shadow: 0upx 20upx 38upx 0upx rgba(219, 165, 3, 0.2) inset;
		border-radius: 0upx 0upx 15upx 15upx;
	}
	
	.foot-reply {
		display: flex;
		flex-direction: column;
	}

	.foot-content {
		display: flex;
		margin-top: 10px;
		width: calc(100% - 32px);
		margin-left: 16px;
		margin-right: 16px;
		justify-content: space-between;
		color: #99989b;
		font-size: 14px;
	}	
	
	.foot-head {
		display: flex;
		margin-top: 10px;
		margin-left: calc(100% - 30px);
		margin-bottom: 7px;
		justify-content: space-between;
		color: #99989b;
		font-size: 12px;
	}
	
	.pentagon {
		width: 28upx;
		height: 27upx;
		margin-right: 12upx;
	}

</style>
